'use strict'
import axios from 'axios'
import Qs from 'qs'

import { Toast, Indicator } from 'mint-ui';
axios.defaults.baseURL = '/';
// axios.defaults.baseURL = 'http://hotel1.deeptel.com.cn';
//
axios.defaults.timeout = 5000;
//`withCredentails`
// axios.defaults.withCredentials = false;
axios.defaults.withCredentials = true;
//
axios.defaults.headers = {
    'X-Requested-With': 'XMLHttpRequest',
    "Content-Type": "application/json",
    'Access-Control-Allow-Headers': "Origin, X-Requested-With, Content-Type, Accept",
    'Access-Control-Allow-Credentials': "true",
};

//
var browser = '';
function isWeiXin() {
    var ua = window.navigator.userAgent.toLowerCase();
    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
        return true;
    } else {
        return false;
    }
}
//
axios.interceptors.request.use(config => {
    Indicator.open({ spinnerType: 'fading-circle' });
    //    1    99 
    if (isWeiXin()) {
        browser = 1;
    } else {
        browser = 99;
    }
    return config
}, error => {
    return Promise.reject(error)
})


// 
axios.interceptors.response.use(function (response) {
    Indicator.close();
    // 
    if (response.data.code != 0 && response.data.code != 1001) {
        checkStatus(response)
        // https://hotel.deeptel.com.cn:443//mobile/78CDF1/home/3
    } else if (response.data.code == 1001) {
        var paeams = encodeURIComponent(JSON.stringify({
            browser: browser,
            uclogin: null,
            returnUrl: window.location.host + '/mobile/78CDF1/home/' + response.data.hotelId,
            busId: response.data.busId
        }));
        location.href = response.data.redirectUrl + paeams;
    }
    return response.data;
    // return response.data;
}, function (error) {
    Indicator.close();
    // 
    return Promise.reject(error);
});
//
function checkStatus(response) {
    // 
    Toast({
        message: response.data.msg,
        duration: 2000
    });
}
//
function checkCode(res) {
    // code(
    // Message.error('');
    // Message.error(res);
    Toast({
        message: "",
        duration: 2000
    });
    return {
        code: 404
    }
}
export default {
    post(url, params) {
        return axios.post(url, params).then(res => {
            return res
        }).catch(err => {
            return checkCode(err)
        })
    },
    get(url, params) {
        return axios.get(url, {
            params: params
        }).then(res => {
            return res
        }).catch(err => {
            return checkCode(err)
        })
    },
    delete(url, params) {
        return axios.delete(url, {
            data: params,
        }).then(res => {
            return res
        }).catch(err => {
            return checkCode(err)
        })
    },
}



